.bg-video--desktop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: -1;

  &__content_desk {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%) scale(1.9);
    transform-origin: 80% 10%;
    animation: zoomOut 7s ease-in-out 2s forwards;
    @include respond(phone) {
      object-position: 70% 10%; 
    }
  }
}

.bg-video--mobile {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-primary-2);
  z-index: 1;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
  border-radius: var(--radius);
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;

    z-index: 10;
    background: rgb(39,52,61);
    animation: FadeIn 1.2s ease-in-out 1s forwards; /* Animation name, duration, easing, delay */

    background: linear-gradient(90deg, rgba(39, 52, 61, 0.823) 0%, rgba(252,70,107,0) 100%);
          }
  &::before {
    content: "";
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;

    width: 100%;
    height: 100%;
    background: rgb(131,58,180);
    animation: FadeIn 1.2s ease-in-out 1s forwards; /* Animation name, duration, easing, delay */

    background: linear-gradient(0deg, #27343d86 0%, rgba(39, 52, 61, 0.923) 41%, rgb(39, 52, 61) 100%);  }
      &__content_mobile {
    position: absolute;
    top: 42.5%;
    left: 0;
    opacity: 0;
    width: 100%;
    transform: scale(0.8); /* Scale the video to 120% of its original size */
    transform-origin: top center; /* Make sure the scaling happens from the center */
    animation:FadeIn2 1.2s ease-in-out 1.5s forwards, zoomIn 7s ease-in-out  forwards;
    filter: saturate(50%);    object-fit: cover;
  

  }
}


